<template>
  <div class="roska-catalog">
    <roska-breadcrumbs/>

    <div class="main">
      <h1 class="title">CATALOG</h1>

      <div class="catalog">
        <div class="options">
          <div class="sex">
            <h5
                class="active"
                @click="changeFilter"
            >ALL</h5>

            <h5
                @click="changeFilter"
            >MAN</h5>

            <h5
                @click="changeFilter"
            >WOMEN</h5>
          </div>

          <div class="filters-only-ds">
            <h3>FILTERS</h3>

            <div class="arrow"></div>
          </div>

          <div class="filters-only-mb"></div>

          <div class="sorting-only-ds">
            <h5>POPULAR</h5>

            <div class="arrow"></div>
          </div>

          <div class="sorting-only-mb"></div>
        </div>

        <div class="items">
          <div class="item"
               v-for="item in items"
               :key="item.name"
          >
            <div class="image"
                 :style="{ backgroundImage: `url(${ getImageUrl(`catalog/items/${ item.src }`) })` }"
            >
              <div class="like" @click="like"></div>

              <div class="addToBag"></div>
            </div>

            <div class="description">
              <div class="name">
                <h5>{{ item.name.toUpperCase() }}</h5>
              </div>

              <div class="cost">
                <h3>€{{ item.cost.toFixed(2) }}</h3>

                <h3 v-if="item.oldCost">€{{ item.oldCost.toFixed(2) }}</h3>
              </div>
            </div>
          </div>
        </div>

        <div class="pagination">
          <div class="arrow-left"></div>

          <div class="pages">
            <h5
                class="active"
                @click="changePage"
            >1</h5>

            <h5
                @click="changePage"
            >2</h5>

            <h5
                @click="changePage"
            >3</h5>

            <h5>...</h5>

            <h5
                @click="changePage"
            >11</h5>
          </div>

          <div class="arrow-right"></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import roskaBreadcrumbs from "@/components/UI-kit/roska-breadcrumbs";
import getImageUrl from "@/mixins/getImageUrl";

export default {
  name: "roska-catalog",

  data() {
    return {
      items: [
        {
          src: "1.png",
          name: "WOMEN'S RIDER JACKET",
          cost: 310,
        },

        {
          src: "2.png",
          name: "Stretch Cotton Trench Coat",
          cost: 270,
        },

        {
          src: "3.png",
          name: "Double-Knit Bomber Jacket",
          cost: 390,
          oldCost: 430,
        },

        {
          src: "4.jpeg",
          name: "TYPE I SHERPA TRUCKER JACKET",
          cost: 190,
        },

        {
          src: "5.png",
          name: "Men's Heritage Drop Shoulder Rider Jacket",
          cost: 190,
        },

        {
          src: "6.png",
          name: "VINTAGE RIDER JACKET RJ19 IN DARK STONEWASH",
          cost: 340,
        },

        {
          src: "7.png",
          name: "MEN'S  STORM RIDER JACKET",
          cost: 590,
        },

        {
          src: "8.png",
          name: "Men's Heritage Drop Shoulder Rider Jacket",
          cost: 330,
        },
      ]
    }
  },

  components: {
    roskaBreadcrumbs,
  },

  methods: {
    getImageUrl,

    changeFilter(event) {
      if (!event.target.classList.contains("active")) {
        const filters = Array.from(document.querySelector(".sex").childNodes)

        filters.forEach(filter => {
          filter.classList.remove("active")
        })

        event.target.classList.add("active")
      }
    },

    changePage(event) {
      if (!event.target.classList.contains("active")) {
        const filters = Array.from(document.querySelector(".pages").childNodes)

        filters.forEach(filter => {
          filter.classList.remove("active")
        })

        event.target.classList.add("active")
      }
    },

    like(event) {
      event.target.classList.toggle("active")
    },
  },

  mounted() {
    document.title = this.$route.meta.title
  },
}
</script>

<style scoped lang="sass">
.roska-catalog
  display: flex
  flex-direction: column
  align-items: flex-start

  @media (min-width: $desktopScreenMinWidth)
    padding-bottom: 130px

  @media (max-width: $mobileScreenMaxWidth)
    width: 100%
    padding-left: 20px
    padding-right: 20px
    padding-bottom: 60px

  .main
    display: flex
    flex-direction: column

    @media (min-width: $desktopScreenMinWidth)
      gap: 60px

    @media (max-width: $mobileScreenMaxWidth)
      gap: 30px

    .title
      text-align: center

      @media (min-width: $desktopScreenMinWidth)
        font-size: 100px
        line-height: 120px

      @media (max-width: $mobileScreenMaxWidth)
        font-size: 60px
        line-height: 72px

    .catalog
      display: flex
      flex-direction: column
      gap: 30px

      .options
        display: flex
        align-items: center

        @media (min-width: $desktopScreenMinWidth)
          gap: 30px

        @media (max-width: $mobileScreenMaxWidth)
          gap: 21px

        .sex
          display: flex
          gap: 10px

          > *
            +box-sizing(border-box)
            +border-radius(999px)
            border: 1px solid $lightgray
            color: $text-lightgray
            cursor: pointer
            line-height: 150%

            @media (min-width: $desktopScreenMinWidth)
              padding: 8px 20px
              font-size: 17px

            @media (max-width: $mobileScreenMaxWidth)
              padding: 4px 12px
              font-size: 15px

            &.active
              background-color: $crimson_red
              border: none
              color: white

              @media (min-width: $desktopScreenMinWidth)
                padding: 9px 21px

              @media (max-width: $mobileScreenMaxWidth)
                padding: 5px 13px

        .filters-only-ds
          @media (min-width: $desktopScreenMinWidth)
            display: flex
            align-items: center
            gap: 10px
            cursor: pointer
            +user-select(none)

            > *
              font-size: 20px
              line-height: 150%

            .arrow
              +background-image-settings()
              background-image: url("/src/assets/images/catalog/options/arrow-bottom.svg")
              width: 16px
              height: 16px

          @media (max-width: $mobileScreenMaxWidth)
            display: none

        .filters-only-mb
          @media (min-width: $desktopScreenMinWidth)
            display: none

          @media (max-width: $mobileScreenMaxWidth)
            +background-image-settings()
            background-image: url("/src/assets/images/catalog/options/filters.svg")
            width: 32px
            height: 32px
            cursor: pointer
            margin-left: auto

        .sorting-only-ds
          @media (min-width: $desktopScreenMinWidth)
            margin-left: auto
            display: flex
            align-items: center
            gap: 10px
            color: $text-lightgray
            cursor: pointer

            > *
              font-size: 15px
              line-height: 150%

            .arrow
              +background-image-settings()
              background-image: url("/src/assets/images/catalog/options/arrow-bottom-gray.svg")
              width: 16px
              height: 16px

          @media (max-width: $mobileScreenMaxWidth)
            display: none

        .sorting-only-mb
          @media (min-width: $desktopScreenMinWidth)
            display: none

          @media (max-width: $mobileScreenMaxWidth)
            +background-image-settings()
            background-image: url("/src/assets/images/catalog/options/sorting.svg")
            width: 32px
            height: 32px
            cursor: pointer

      .items
        justify-content: center
        display: flex
        flex-wrap: wrap
        row-gap: 30px
        column-gap: 20px

        @media (min-width: $desktopScreenMinWidth)
          max-width: calc(289px * 4 + 20px * 3)

        @media (max-width: $mobileScreenMaxWidth)

        .item
          display: flex
          flex-direction: column
          gap: 20px

          @media (min-width: $desktopScreenMinWidth)
            width: 289px

          @media (max-width: $mobileScreenMaxWidth)
            width: 320px

          .image
            height: 400px
            +background-image-settings()
            +border-radius(20px)
            pointer-events: none
            position: relative

            @media (min-width: $desktopScreenMinWidth)
              width: 289px

            @media (max-width: $mobileScreenMaxWidth)
              width: 320px

            .like
              +background-image-settings()
              background-image: url("/src/assets/images/catalog/items/like.svg")
              width: 32px
              height: 32px
              position: absolute
              top: 15px
              right: 15px
              cursor: pointer
              pointer-events: all

              &.active
                background-image: url("/src/assets/images/catalog/items/liked.svg")

            .addToBag
              +background-image-settings()
              background-image: url("/src/assets/images/catalog/items/addToBag.svg")
              width: 32px
              height: 32px
              position: absolute
              bottom: 15px
              right: 15px
              cursor: pointer
              pointer-events: all

          .description
            display: flex
            flex-direction: column
            gap: 10px
            height: 100%
            flex: 1

            .name
              > *
                font-size: 20px
                line-height: 110%

            .cost
              display: flex
              gap: 10px
              margin-top: auto

              > *
                font-size: 20px
                line-height: 130%

                &:nth-child(2)
                  text-decoration: line-through
                  color: $text-lightgray

      .pagination
        display: flex
        gap: 20px
        align-items: center
        margin-top: 30px
        justify-content: center

        .arrow-left
          +background-image-settings()
          background-image: url("/src/assets/images/catalog/pagination/arrow-left.svg")
          width: 16px
          height: 16px
          cursor: pointer

        .pages
          display: flex
          gap: 10px

          > *
            font-size: 20px
            line-height: 150%
            color: $text-gray
            width: 44px
            height: 44px
            background-color: $paginationGray
            display: flex
            align-items: center
            justify-content: center
            text-align: center
            +border-radius(100px)
            cursor: pointer

            &.active
              background-color: $crimson_red
              color: white

        .arrow-right
          +background-image-settings()
          background-image: url("/src/assets/images/catalog/pagination/arrow-right.svg")
          width: 16px
          height: 16px
          cursor: pointer
</style>
