import {createRouter, createWebHistory} from "vue-router";

import roskaMain from "@/components/templates/roska-main";
import roskaCatalog from "@/components/templates/roska-catalog";

export default createRouter({
    history: createWebHistory(),
    routes: [
        {
            path: '/roska-test/',
            name: "main",
            component: roskaMain,
            meta: {
                title: "ROSKA - Main"
            },
        },

        {
            path: '/roska-test/catalog',
            name: "catalog",
            component: roskaCatalog,
            meta: {
                title: "ROSKA - Catalog"
            },
        },
    ],
    scrollBehavior(to) {
        if (to.hash) {
            return {
                el: to.hash,
                behavior: 'smooth',
            }
        }
    }
})
